@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Stencil+Display&display=swap);


h1 {
    line-height: 1.5;
    font-weight: 350;
    font-family: "Georgia", Arial, sans-serif;
    color: #000; }

.space-section {
    padding: 6em 0; }

.heading-section {
    font-size: 20px;
    color: #000;
}

.img {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

.con-control {
    position: relative;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 10px 34px -15px rgba(0, 0, 0, 0.20);
}
.con-control h3 {
    font-weight: 300;
}
.con-control .icon {
    width: 80px;
    height: 80px;
    background: #1089ff;
    border-radius: 50%;
    font-size: 30px;
    margin: 0 auto;
    margin-bottom: 10px;
}
.con-control .icon span {
    color: #fff; }

.form-control {
    height: 50px;
    background: #fff;
    color: #000;
    font-size: 15px;
    border-radius: 4px;
    box-shadow: none;
    border: 1px solid rgba(0, 0, 0, 0.1);

}
.form-control:focus, .form-control:active {
    outline: none ;
    box-shadow: none;
    border: 1px solid #1089ff; }
.btn {
    cursor: pointer;
    border-radius: 40px;
    box-shadow: none ;
    font-size: 15px;
    margin-top: 10px;
}
.btn.btn-primary {
    background: #1089ff ;
    border: 1px solid #1089ff ;
    color: #fff ;
}
.btn:hover, .btn:active, .btn:focus {
    outline: none;
}
.forgot1 {
    text-align: right;
    margin-right: 30px;
    margin-top: 10px;
}
.register{
    margin-top: 10px;
}

img{
    width: 50px;
    height: 50px;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
    border-radius: 50%;
}
.drop-nav{
    position: relative;
    display: inline-block;
}
.drop-nav a{
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
.image{
    color: #f6f7fa;
    position: relative;
}
.drop-nav .dropdown{
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
    z-index: 1;
    color: #000;
    font-size: 12px;
    -webkit-appearance: none;
}
.drop-nav .dropdown li{
    display: block;

}
.drop-nav e{
    color: white;
}
.drop-nav:hover .dropdown{
    display: block;
}

.errMsg{
    background: rgb(214, 10, 10);
    color: #fff9;
    text-align: center;
    padding: 10px 0;
    letter-spacing: 1.3px;
}

.successMsg{
    background: rgb(9, 158, 54);
    color: #fff9;
    text-align: center;
    padding: 10px 0;
    letter-spacing: 1.3px;
}
.profile_page{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
.profile_page .col-left{
    max-width: 400px;
    min-width: 300px;
    padding: 0 10px;
    margin: auto;
}

.profile_page .col-left .avatar{
    width: 150px;
    height: 150px;
    overflow: hidden;
    position: relative;
    margin: 15px auto;
    border: 1px solid #ddd;
    border-radius: 50%;
    cursor: pointer;
}
.profile_page .col-left img{
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
}

.profile_page .col-left .avatar span{
    position: absolute;
    bottom: -100%;
    left: 0;
    width: 100%;
    height: 50%;
    background: #fff8;
    text-align: center;
    font-weight: 400;
    text-transform: uppercase;
    color: rgb(255, 140, 45);
    transition: 0.3s ease-in-out;
}
.profile_page .col-left .avatar:hover span{
    bottom: -25%;
}

.App {
